<template>
  <div>
    <div class="box" ref="wrapper">
      <ul>
        <li class="singer-title" v-for="data of singerList" :key="data.id" @click="handleSingerClick(data.id)">
          <img class="image" v-lazy="data.img1v1Url" alt="">
          <div class="singer-name">{{data.name}}</div>
        </li>
        <li class="none" v-show="this.$store.state.duration"></li>
      </ul>
    </div>
  <router-view></router-view>
  </div>
</template>

<script>
import {getSingerList} from '@/api/singer'
import BScroll from 'better-scroll'

export default {
  name: 'Singer',
  data () {
    return {
      singerList: []
    }
  },
  created () {
    this._getSingerList()
  },
  mounted () {
    this.scroll = new BScroll(this.$refs.wrapper, {
      click: true
    })
  },
  methods: {
    handleSingerClick (id) {
      this.$router.push({
        path: '/singer/' + id
      })
    },
    swiperight () {
      this.$router.push({
        'path': '/rank'
      })
    },
    _getSingerList () {
      getSingerList().then((res) => {
        this.singerList = res.data.artists
      })
    }
  }
}
</script>

<style lang="stylus" scoped>
  .box
    position absolute
    top 5.7rem
    left 0
    height calc(100vh - 5.7rem)
    overflow hidden
    .singer-title
      display flex
      height 60px
      .image
        width 50px
        height 50px
        border-radius 50%
        margin 5px 10px
      .singer-name
        line-height 60px
        font-size 15px
    .none
      height 60px
      width 100vw
      background #fff
</style>



